.topbar{
    background-color: #2c3e50;
    width: 100%;
    height:fit-content;
    margin:0;
    padding:0;
    padding-left: 10px;
  
    display:flex;
    align-items:center;
  }
.topbar-name{
    text-transform: uppercase;
    width:fit-content;
    height: fit-content;

    margin-right:100px;
    margin-left:100px;
}

.landing-options{
    height: 100%;
  
    flex:1;
  
    display:flex;
    align-items: center;
    justify-content: center;
    
    border: 3px;
    border-radius: 15px;
}



.landing-option-item{
    user-select: none;
    
    height:100%;

    flex:1;
  
    background-color: #7f8c8d;
    
    
    border-radius: 7px;
    border-width: 0px;
  
    transition: background-color 300ms;
    transition: border-radius 300ms;

    margin-right:10px;
    margin-left:10px;
}

.landing-option-item:hover{
    background-color:#95a5a6;
    border-radius: 15px;
}