.education-header{
    width: 100vw;
    display: flex;
    justify-content: center;
}

.education-main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex:1;
}

.education-content{
    flex:1;
    display:flex;
}

.education-element{
    flex:1;
    display:flex;
    flex-direction: column;
    height:100%;
    justify-content: center;
    overflow:hidden;
}


.education-element:hover >.education-dropdown-container > .education-dropdown{
    height:100%;

    transition: height 300ms ease-out;
}


.education-dropdown-container{
    display:flex;
    flex:1;
    align-items:start;
}

.education-dropdown{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    height:0%;
    width:100%;
    overflow:hidden;
    
    display: flex;
    align-content: center;

    transition: height 300ms ease-in;
}


h2{
    display: flex;
    align-items: center;
    justify-content: center;
}

h3{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}