body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  padding:0px;
  margin:0px;
  font-family:'Inconsolata';
  color:#d6dee3;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{
  position: absolute;
  height: 100%;
  width:100%;
  
  padding:0px;
  margin:0px;
  z-index:  -2;
  
  display: flex;
  flex-direction: column;

  overflow:hidden;
  background-color: #4b6163;
}

.background-image{
  position: absolute;
  height: 100%;
  width:100%;
  
  padding:0px;
  margin:0px;

  top:0%;
  right:0%;
  bottom:0%;
  left:0%;
  z-index:  -1;

  filter:blur(8px);

  background-image: url(https://mir-s3-cdn-cf.behance.net/project_modules/max_1200/37b3e541018013.579633f706150.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}


.my-node-enter {
  transform:translateX(-100%);
}
.my-node-enter-active {
  transform:translateX(0%);

  transition:all 400ms;
  transition-timing-function: ease-in-out;

}
.test{
  display: flex;
  grid-area: 1 / 1 / 2 / 2;
}

.my-node-exit-active {
  transform:translateX(100%);

  transition:all 400ms;
  transition-timing-function: ease-in-out;
}

.content{
  display:grid;
  flex:1;
}

.fill-in-panel{
  display:flex;
  flex:1;
  background-color: aqua;
}

.background-canvas{
  position:absolute;
}

canvas{
  height:100%;
  width:100%;
  z-index: -100;
}