.projects-vertical-flex{
    display: flex;
    flex-direction: column;
    margin:15px;
    flex:1;
}

.projects-horizontal-flex{
    flex:1;
    display: flex;
    flex-direction: row;
}

.projects-element-container{
    flex:1;
    display: flex;
}

.projects-element{
    flex:1;
    margin: 15px;
    
    transition:all 200ms;
    background-color: aqua;
}

.projects-element:hover{
    margin: 5px;
    
    transition:all 200ms;
}